import { IProudList } from './../../page.base';
import { TranslateService } from '@ngx-translate/core';
import { ClubProvider } from './../../../providers/club-provider';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { PageBusiness } from '../../page';
import { Webpage } from '../../../models/models';
import { WidgetProvider } from '../../../providers/providers';

/**
 * Generated class for the CommunitySearchSingleItemPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */
interface ParamModel {
    type: string,
    datasource: any,
    isPagination: boolean,
    searchKey: string
}

interface typeModel {
  type: string,
  translateKey: string 
}
@IonicPage()
@Component({
  selector: 'page-community-search-single-item',
  templateUrl: 'community-search-single-item.html',
})
export class CommunitySearchSingleItemPage implements PageBusiness, IProudList {

  title: string 
  types: typeModel[] = []
  Params: ParamModel = {
    type: '',
    datasource: [],
    isPagination: false,
    searchKey: ''
  } 
  type: string;
  hasMoreData:  boolean = false;
  isDataLoading: boolean = false;
  isDataEmpty: boolean = false;
  pageCount: number = 20;
  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public clubProvider: ClubProvider,
    public translate: TranslateService,
    public widgetProvider: WidgetProvider) { 
      this.types = [{
        type: 'activities',
        translateKey: 'SOCIAL_CLUB_ACTIVITY'
      },{
        type: 'boards',
        translateKey: 'SOCIAL_CLUB_CLUB'
      },{
        type: 'posts',
        translateKey: 'SOCIAL_CLUB_MOMENT'
      },{
        type: 'topics',
        translateKey: 'SOCIAL_CLUB_POSTS'
      }]

      this.Params = this.navParams.data   
      this.type = this.Params.type
      this.hasMoreData = this.Params.datasource.length == 10
      this.title = translate.instant(this.types.find(p => p.type == this.Params.type).translateKey) 
  }

 
  searchMore(){
    this.clubProvider.SearchAllPosts(this.Params.searchKey, this.Params.datasource.length, this.pageCount).subscribe(res=>{

    },err=>{

    },()=>{

    })
  }

  parsePicture(pics: string){
    if(!pics)return ''; 
    return pics.split(',')[0] 
  }
  doInfinite(_event){  
      this.clubProvider.SearchAllPosts(this.Params.searchKey, this.Params.datasource.length, this.pageCount).subscribe(res=>{ 
        if(res.result){
          this.Params.datasource.push(...res.result)
        }
        this.hasMoreData = res.result.length == this.pageCount
        
      },err=>{
  
      },()=>{
        _event.complete();
      }) 
  }
 
  doRefresh(_event){
    _event.complete(); 
  }


  
  TopicDetail(topicId: number){ 
    this.clubProvider.openWebpage(this.clubProvider.TopicDetail(topicId), this.navCtrl) 
  }

  ActivityDetail(activityId: number){  
    this.clubProvider.openWebpage(this.clubProvider.ActivityDetail(activityId), this.navCtrl) 
  }

  clubDetail(boardId: number){ 
    this.clubProvider.openWebpage(this.clubProvider.BoardDetail(boardId,"",""), this.navCtrl) 
  }

  PostDetail(postId: number){
    this.clubProvider.openWebpage(this.clubProvider.PostDetail(postId), this.navCtrl) 
  }
 
}



